@import "https://fonts.googleapis.com/css2?family=Ultra&display=swap";
@import "https://fonts.googleapis.com/css2?family=Orelega+One&family=Tilt+Warp&display=swap";
@import "https://fonts.googleapis.com/css2?family=Oswald:wght@200..700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap";
*, :before, :after {
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  line-height: 1.6;
  list-style: none;
}

body {
  background-color: #fff;
}

a {
  text-decoration: none;
}

.content {
  background-color: #fff;
  width: 80%;
  min-height: 100vh;
  margin: 25px auto;
}

.content__titles {
  border-bottom: 1px solid #323232;
  margin-bottom: 30px;
  font-family: Oswald, sans-serif;
  font-size: 1.3rem;
}

.content .event {
  border-bottom: 1px solid #d3d3d3;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0 auto 50px;
  display: -webkit-flex;
  display: flex;
}

.content .event__info {
  margin-bottom: 20px;
  font-family: Open Sans, sans-serif;
}

.content .event__info p:first-child {
  font-family: Oswald, sans-serif;
  font-weight: 800;
}

.content .event__button {
  color: #fff;
  cursor: pointer;
  background: -moz-linear-gradient(90deg, #e21d5a 0%, #f30 40%, #ffa800 65%, #e21d5a 100%) 0 0 / 300% 300%;
  background: linear-gradient(90deg, #e21d5a 0%, #f30 40%, #ffa800 65%, #e21d5a 100%) 0 0 / 300% 300%;
  border: none;
  border-radius: 10px;
  padding: 10px 30px;
  font-family: Open Sans, sans-serif;
  font-size: 1rem;
  -moz-transition: all .4s ease-in;
  transition: all .4s ease-in;
  position: relative;
  box-shadow: 0 2px 25px #ff008280;
}

.content .event__button:hover {
  background-position: 100% 100%;
}

@media (width <= 575.999px) {
  .content__info {
    -moz-box-orient: vertical;
    -moz-box-direction: normal;
    -webkit-flex-direction: column;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

@media (width >= 576.001px) and (width <= 768px) {
  .content {
    width: 75%;
  }
}

@media (width >= 768.001px) and (width <= 992px) {
  .content {
    width: 70%;
  }
}

@media (width >= 992.001px) and (width <= 1200px) {
  .content {
    width: 60%;
  }
}

@media (width >= 1200px) {
  .content {
    width: 50%;
  }
}
